html,
body {
  height: 100%;
  background-color: var(--black);
}

:root {
  --black: black;
  --white: white;
  --camo-green: #78866B;
  --sea-glass-green: #98b796;
  --navy: #000080;
  --green: #004d00;



}

.background {

  background-repeat: no-repeat;
  background-attachment: fixed;
}



